.card {
    -webkit-box-shadow: 0 3px 6px #999, 0 3px 6px #999;
            box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    width:20%;
    margin: 15px 15px;
    background: none;
  }

  .image-container img {
    max-width: 100%;
    border: none;
  }

  .image-container:hover {
    -webkit-transform: scale(1.0625) translateY(-2px);
        -ms-transform: scale(1.0625) translateY(-2px);
            transform: scale(1.0625) translateY(-2px);
    -webkit-transition: all 300ms ease-in-out;
    -o-transition: all 300ms ease-in-out;
    transition: all 300ms ease-in-out;
  }

  .image-container {
    cursor: pointer;
  }


